import { getComments } from '@/api/index'

export default {
  name: "article-comments",
  props:{
    article:{
      type:Object,
      required :true
    }
  },
  data(){
    return{
      comments:[]
    }
  },
  async mounted() {
    const { data } = await getComments(this.article.slug)
    this.comments = data.comments
  }
}
